.page-link-status-wrapper {
  display: block;
  margin: 1rem 0; }

@media screen and (min-width: 600px) {
  .page-link-status-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr); } }

table.blc-results-full {
  width: 100%;
  max-width: 100%;
  border-spacing: 0;
  border-collapse: collapse; }
  table.blc-results-full th {
    background-color: var(--esm-c-gray-10); }
  table.blc-results-full tr {
    border-bottom: 1px solid var(--esm-c-gray-15); }
    table.blc-results-full tr td {
      padding: 0 1rem; }
  table.blc-results-full tr.page-row {
    border-top: 2px solid var(--esm-c-gray-50);
    border-left: 3px solid var(--esm-c-success-dark);
    background-color: var(--esm-c-gray-5); }
    table.blc-results-full tr.page-row.has-redirect {
      border-left-color: var(--esm-c-warning-dark); }
    table.blc-results-full tr.page-row.has-error {
      border-left-color: var(--esm-c-error-dark); }
    table.blc-results-full tr.page-row td {
      padding: 0.75rem; }
    table.blc-results-full tr.page-row > td {
      padding-right: 0; }
  table.blc-results-full .redirect-row,
  table.blc-results-full .link-row {
    display: none; }
    table.blc-results-full .redirect-row.status-400,
    table.blc-results-full .link-row.status-400 {
      display: table-row; }

.field__item__status_bar {
  width: 100%;
  height: 1.5rem;
  background: var(--esm-c-gray-5);
  border: 1px solid var(--esm-c-gray-25);
  position: relative;
  border-radius: 3px;
  overflow: hidden; }
  .field__item__status_bar .field__item__status_bar__ind {
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--esm-c-success-dark); }
